import { getComments, addComments, deleteComments } from '@/api/article';

export default {
  name: 'ArticleComment',
  props: {
    article: {
      type: Object,
      required: true,
      default: () => {
        return {};
      },
    },
    user: {
      type: [Object, null],
      required: true,
      default: null,
    },
  },

  data() {
    return {
      disabled: false,
      comment: {
        body: '',
      },
      comments: [],
    };
  },

  mounted() {
    this.getCommentsData();
  },

  methods: {
    /**
     * 获取评论数据
     */
    async getCommentsData() {
      try {
        const { data: { comments = [] } = {} } = await getComments(
          this.article.slug,
        );
        this.comments = comments;
      } catch (error) {}
    },

    /**删除评论 */
    async onHandleDelete(id) {
      this.comments = this.comments.filter((item) => {
        return item.id !== id;
      });
      // await deleteComments(this.article.slug, id);
    },

    /**提交评论 */
    async onSubmit() {
      if (!this.comment.body) return;
      this.disabled = true;

      this.comments.unshift({
        id: 100000 * Math.random(),
        createdAt: new Date(),
        updatedAt: new Date(),
        body: this.comment.body,
        author: {
          ...this.user,
        },
      });
      // const { data } = await addComments(this.article.slug, {
      //   comment: this.comment,
      // });
      this.disabled = false;
    },
  },
};
